'use client'

import { useState, useEffect } from 'react'
import axios from 'axios'
import { ArrowLeft, Loader2, Play, Hash, TrendingUp, Trophy, Star, Download, Calendar, Sparkles, History, ArrowUpRight, Minus, Users, LayoutGrid, MessageSquare, ShieldCheck, ChevronRight, Check, Info, AppWindow } from 'lucide-react'
import Link from 'next/link'
import { motion, AnimatePresence } from 'framer-motion'
import { useSearchParams } from 'next/navigation'
import { Suspense } from 'react'

const countries = [
    { code: 'us', name: 'United States', flag: 'ðŸ‡ºðŸ‡¸' },
    { code: 'in', name: 'India', flag: 'ðŸ‡®ðŸ‡³' },
    { code: 'gb', name: 'United Kingdom', flag: 'ðŸ‡¬ðŸ‡§' },
    { code: 'ca', name: 'Canada', flag: 'ðŸ‡¨ðŸ‡¦' },
    { code: 'au', name: 'Australia', flag: 'ðŸ‡¦ðŸ‡º' },
    { code: 'de', name: 'Germany', flag: 'ðŸ‡©ðŸ‡ª' },
    { code: 'fr', name: 'France', flag: 'ðŸ‡«ðŸ‡·' },
    { code: 'br', name: 'Brazil', flag: 'ðŸ‡§ðŸ‡·' },
]

function TrackerContent() {
    const searchParams = useSearchParams()
    const appId = searchParams.get('appId')
    const initialTitle = searchParams.get('title')
    const initialIcon = searchParams.get('icon')
    const initialDeveloper = searchParams.get('developer')
    const initialCountry = searchParams.get('country') || 'us'

    const selectedCountry = countries.find(c => c.code === initialCountry)

    const [keyword, setKeyword] = useState('')
    const [isCheckingRank, setIsCheckingRank] = useState(false)
    const [rankResult, setRankResult] = useState(null)
    const [country, setCountry] = useState(initialCountry)

    const [appDetails, setAppDetails] = useState(null)
    const [isLoadingDetails, setIsLoadingDetails] = useState(false)

    const [history, setHistory] = useState([])
    const [suggestions, setSuggestions] = useState([])
    const [similarApps, setSimilarApps] = useState([])
    const [developerApps, setDeveloperApps] = useState([])

    const [isLoadingExtra, setIsLoadingExtra] = useState(false)
    const [activeTab, setActiveTab] = useState('similar')

    useEffect(() => {
        if (appId) {
            fetchAppDetails()
            loadHistory()
            fetchExtraFeatures()
        }
    }, [appId])

    useEffect(() => {
        if (keyword.length > 2) {
            fetchSuggestions()
        } else {
            setSuggestions([])
        }
    }, [keyword])

    const fetchAppDetails = async () => {
        setIsLoadingDetails(true)
        try {
            const { data } = await axios.get(`/api/apps/details?appId=${appId}&country=${country}`)
            setAppDetails(data)
        } catch (error) {
            console.error('Failed to fetch details')
        } finally {
            setIsLoadingDetails(false)
        }
    }

    const fetchExtraFeatures = async () => {
        setIsLoadingExtra(true)
        try {
            const similarRes = await axios.get(`/api/apps/similar?appId=${appId}`)
            setSimilarApps(similarRes.data.slice(0, 8))



            // Dynamic developer lookup
            const devId = appDetails?.developerId || initialDeveloper
            const devRes = await axios.get(`/api/apps/developer?devId=${devId}`)
            setDeveloperApps(devRes.data.slice(0, 8))
        } catch (error) {
            console.error('Failed to fetch extra features')
        } finally {
            setIsLoadingExtra(false)
        }
    }

    const fetchSuggestions = async () => {
        try {
            const { data } = await axios.get(`/api/keywords/suggest?term=${keyword}`)
            setSuggestions(data.slice(0, 5))
        } catch (error) {
            console.error('Failed to fetch suggestions')
        }
    }

    const loadHistory = () => {
        if (typeof window !== 'undefined') {
            const savedHistory = localStorage.getItem(`history_${appId}`)
            if (savedHistory) setHistory(JSON.parse(savedHistory))
        }
    }

    const saveToHistory = (result) => {
        if (typeof window !== 'undefined') {
            const newEntry = { ...result, id: Date.now(), timestamp: new Date().toISOString() }
            const updatedHistory = [newEntry, ...history].slice(0, 8)
            setHistory(updatedHistory)
            localStorage.setItem(`history_${appId}`, JSON.stringify(updatedHistory))
        }
    }

    const checkRank = async (e, customTerm = null) => {
        if (e) e.preventDefault()
        const termToCheck = customTerm || keyword.trim()
        if (!appId || !termToCheck) return

        if (customTerm) setKeyword(customTerm)

        setIsCheckingRank(true)
        setRankResult(null)
        try {
            const { data } = await axios.post('/api/keywords', { appId, term: termToCheck, country })
            setRankResult(data)
            saveToHistory(data)
        } catch (error) {
            console.error('Failed to check rank')
        } finally {
            setIsCheckingRank(false)
        }
    }

    if (!appId) return (
        <div className="flex flex-col items-center justify-center py-20 text-slate-500">
            <Info className="w-10 h-10 mb-4 opacity-20" />
            <p className="font-bold uppercase tracking-widest text-xs">Access Denied: No App Selected</p>
            <Link href="/" className="mt-4 pro-button text-xs">Initialize Discovery</Link>
        </div>
    )

    return (
        <div className="max-w-7xl mx-auto px-6 py-10 space-y-10">
            {/* Header: Compact Expert Layout */}
            <div className="flex flex-col lg:flex-row lg:items-center justify-between gap-6">
                <div className="flex items-center gap-6">
                    <div className="relative">
                        <img src={appDetails?.icon || initialIcon} className="w-20 h-20 rounded-2xl shadow-2xl border border-white/10" />
                        <div className="absolute -top-2 -right-2 bg-blue-600 rounded-full p-1.5 shadow-lg border-2 border-[#05070a]">
                            <AppWindow className="w-3 h-3 text-white" />
                        </div>
                    </div>
                    <div>
                        <Link href="/" className="flex items-center gap-1 text-slate-500 hover:text-blue-400 text-[10px] font-black uppercase tracking-widest mb-1 transition-colors">
                            <ArrowLeft className="w-3 h-3" /> Back to Dashboard
                        </Link>
                        <h1 className="text-3xl font-black text-white leading-none mb-1">{appDetails?.title || initialTitle}</h1>
                        <p className="text-slate-500 text-sm font-bold">{appDetails?.developer || initialDeveloper}</p>
                    </div>
                </div>

                <div className="flex items-center gap-2 overflow-x-auto no-scrollbar pb-2">
                    <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl flex items-center gap-3">
                        <Star className="w-4 h-4 text-yellow-500 fill-yellow-500" />
                        <div className="text-sm font-black text-white">{appDetails?.score?.toFixed(1) || '0.0'}</div>
                        <div className="text-[10px] text-slate-500 uppercase font-black tracking-widest border-l border-white/10 pl-3">Rating</div>
                    </div>
                    <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl flex items-center gap-3">
                        <Download className="w-4 h-4 text-emerald-500" />
                        <div className="text-sm font-black text-white">{appDetails?.installs || 'N/A'}</div>
                        <div className="text-[10px] text-slate-500 uppercase font-black tracking-widest border-l border-white/10 pl-3">Installs</div>
                    </div>

                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-4 gap-10">
                {/* Left: Main Track Area */}
                <div className="lg:col-span-3 space-y-10">
                    {/* Rank Checker Card */}
                    <div className="glass-card p-8 relative overflow-hidden">
                        <div className="absolute top-0 right-0 p-8 opacity-[0.02] pointer-events-none">
                            <Trophy className="w-40 h-40" />
                        </div>

                        <div className="flex items-center gap-3 mb-8">
                            <div className="bg-blue-600 p-2.5 rounded-xl">
                                <TrendingUp className="w-5 h-5 text-white" />
                            </div>
                            <h2 className="text-xl font-black text-white uppercase italic tracking-tight">Keyword Tracking</h2>
                        </div>

                        <form onSubmit={checkRank} className="flex flex-col sm:flex-row gap-3 relative z-10">
                            <div className="relative flex-1 group">
                                <Hash className="absolute left-5 top-1/2 -translate-y-1/2 text-slate-600 w-5 h-5" />
                                <input
                                    type="text"
                                    placeholder="Enter target keyword (e.g. money manager)..."
                                    className="pro-input pl-14 text-lg py-5"
                                    value={keyword}
                                    onChange={(e) => setKeyword(e.target.value)}
                                />
                            </div>
                            <button
                                type="submit"
                                disabled={isCheckingRank || !keyword.trim()}
                                className="pro-button px-10 py-5 text-lg font-black uppercase italic tracking-tighter"
                            >
                                {isCheckingRank ? <Loader2 className="w-6 h-6 animate-spin" /> : <Sparkles className="w-6 h-6" />}
                                Analyze
                            </button>
                        </form>

                        <AnimatePresence>
                            {suggestions.length > 0 && (
                                <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex flex-wrap gap-2 mt-6">
                                    <span className="text-[9px] font-black uppercase tracking-widest text-slate-600 mr-2 flex items-center gap-1">
                                        <Info className="w-3 h-3" /> Related queries:
                                    </span>
                                    {suggestions.map((s, i) => (
                                        <button key={i} onClick={() => checkRank(null, s)} className="text-[11px] font-bold text-slate-400 hover:text-white bg-white/5 hover:bg-white/10 px-3 py-1 rounded-md transition-all border border-white/5">
                                            {s}
                                        </button>
                                    ))}
                                </motion.div>
                            )}
                        </AnimatePresence>
                    </div>

                    {/* Rank Result: Efficient Visualization */}
                    <AnimatePresence mode="wait">
                        {rankResult && (
                            <motion.div key={rankResult.term + rankResult.position} initial={{ opacity: 0, scale: 0.98 }} animate={{ opacity: 1, scale: 1 }} exit={{ opacity: 0 }}>
                                <div className="glass-card grid md:grid-cols-2 overflow-hidden">
                                    <div className="p-12 flex flex-col items-center justify-center bg-blue-600/5 relative">
                                        <div className="text-[8rem] font-black text-white leading-none tracking-tighter italic drop-shadow-2xl">
                                            {typeof rankResult.position === 'string' && rankResult.position.includes('Found') ? 'HIT' : rankResult.position === '1000+' ? 'OUT' : `#${rankResult.position}`}
                                        </div>
                                        <div className="mt-4 px-4 py-1.5 bg-blue-600/20 border border-blue-600/30 rounded-full text-[10px] font-black uppercase tracking-widest text-blue-400">
                                            Quantum Rank Result
                                        </div>
                                    </div>
                                    <div className="p-12 flex flex-col justify-center space-y-6">
                                        <div>
                                            <h3 className="text-2xl font-black text-white uppercase tracking-tight italic">
                                                {typeof rankResult.position === 'string' && rankResult.position.includes('Found') ? "Variation Match Found" : rankResult.position === '1000+' ? "No visibility found" : "Keyword Visibility Detected"}
                                            </h3>
                                            <p className="text-slate-500 mt-1 font-bold">Query: <span className="text-blue-400">"{rankResult.term}"</span></p>
                                        </div>

                                        <p className="text-sm text-slate-400 leading-relaxed">
                                            {rankResult.position === '1000+' ? "We scanned the Top 1000 and couldn't find a direct match. Try niche variations below." : "The app is currently indexing within the reachable range. Deep scans are active to monitor position changes."}
                                        </p>

                                        {rankResult.position === '1000+' && suggestions.length > 0 && (
                                            <div className="pt-4 border-t border-white/5">
                                                <div className="flex flex-wrap gap-2">
                                                    {suggestions.map((s, i) => (
                                                        <button key={i} onClick={() => checkRank(null, s)} className="pro-button py-2 px-4 text-xs">Try {s}</button>
                                                    ))}
                                                </div>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>

                    {/* Intelligence Hub: Redesigned for Efficiency */}
                    <div className="space-y-6">
                        <div className="flex items-center justify-between gap-4 border-b border-white/5 pb-2">
                            <div className="flex items-center gap-3">
                                <ShieldCheck className="w-5 h-5 text-blue-500" />
                                <h3 className="text-sm font-black text-white uppercase tracking-[0.2em]">Expert Intelligence</h3>
                            </div>
                            <div className="flex gap-1">
                                {[
                                    { id: 'similar', icon: Users, label: 'Competitors' },
                                    { id: 'developer', icon: LayoutGrid, label: 'Portfolio' },

                                ].map((tab) => (
                                    <button key={tab.id} onClick={() => setActiveTab(tab.id)} className={`px-4 py-2 rounded-lg text-xs font-bold transition-all flex items-center gap-2 ${activeTab === tab.id ? 'bg-blue-600 text-white' : 'text-slate-500 hover:text-white'}`}>
                                        <tab.icon className="w-3 h-3" /> {tab.label}
                                    </button>
                                ))}
                            </div>
                        </div>

                        <div className="min-h-[300px]">
                            <AnimatePresence mode="wait">
                                {isLoadingExtra ? (
                                    <motion.div key="load" initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex flex-col items-center py-24 text-slate-600">
                                        <Loader2 className="w-8 h-8 animate-spin mb-4" />
                                        <p className="text-[10px] font-black uppercase tracking-widest">Compiling Dataset...</p>
                                    </motion.div>
                                ) : activeTab === 'similar' ? (
                                    <motion.div key="sim" initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-4 gap-4">
                                        {similarApps.map((app) => (
                                            <Link key={app.appId} href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}&country=${country}`}>
                                                <div className="glass-card p-4 hover:border-blue-500/30 group">
                                                    <div className="flex gap-3 mb-3">
                                                        <img src={app.icon} className="w-10 h-10 rounded-lg grayscale group-hover:grayscale-0 transition-all" />
                                                        <div className="flex-1 min-w-0">
                                                            <h4 className="text-[11px] font-black text-white truncate uppercase">{app.title}</h4>
                                                            <p className="text-[9px] text-slate-500 font-bold truncate">{app.developer}</p>
                                                        </div>
                                                    </div>
                                                    <div className="flex items-center justify-between text-[9px] font-black uppercase text-slate-600 pt-2 border-t border-white/5">
                                                        <span>Scored {app.score?.toFixed(1)}</span>
                                                        <ChevronRight className="w-3 h-3" />
                                                    </div>
                                                </div>
                                            </Link>
                                        ))}
                                    </motion.div>
                                ) : (
                                    <motion.div key="dev" initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-4 gap-4">
                                        {developerApps.map((app) => (
                                            <Link key={app.appId} href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}&country=${country}`}>
                                                <div className="glass-card p-4 hover:border-blue-500/30 group">
                                                    <div className="flex gap-3 mb-3">
                                                        <img src={app.icon} className="w-10 h-10 rounded-lg grayscale group-hover:grayscale-0 transition-all" />
                                                        <div className="flex-1 min-w-0">
                                                            <h4 className="text-[11px] font-black text-white truncate uppercase">{app.title}</h4>
                                                            <p className="text-[9px] text-slate-500 font-bold truncate">Installs: {app.installs}</p>
                                                        </div>
                                                    </div>
                                                    <div className="flex items-center justify-between text-[9px] font-black uppercase text-blue-500 pt-2 border-t border-white/5">
                                                        <span>Internal Asset</span>
                                                        <ChevronRight className="w-3 h-3 text-slate-600" />
                                                    </div>
                                                </div>
                                            </Link>
                                        ))}
                                    </motion.div>
                                )}
                            </AnimatePresence>
                        </div>
                    </div>
                </div>

                {/* Right: Sidebar Activity */}
                <div className="space-y-8">
                    <section className="space-y-4">
                        <div className="flex items-center gap-3 border-b border-white/5 pb-3">
                            <History className="w-4 h-4 text-slate-500" />
                            <h2 className="text-xs font-black text-white uppercase tracking-widest mt-0.5">Execution Log</h2>
                        </div>

                        <div className="space-y-2">
                            {history.map((entry) => (
                                <motion.div key={entry.id} initial={{ x: 5, opacity: 0 }} animate={{ x: 0, opacity: 1 }} className="bg-white/[0.02] border border-white/10 p-4 rounded-xl flex items-center justify-between group cursor-pointer hover:bg-white/5 transition-all" onClick={() => checkRank(null, entry.term)}>
                                    <div className="min-w-0">
                                        <div className="text-[10px] font-black text-slate-200 uppercase truncate mb-0.5">{entry.term}</div>
                                        <div className="text-[9px] text-slate-600 font-bold">{new Date(entry.timestamp).toLocaleTimeString()}</div>
                                    </div>
                                    <div className="flex items-center gap-2">
                                        <span className={`text-xs font-black ${entry.position === '1000+' ? 'text-slate-600' : 'text-blue-500'}`}>
                                            {typeof entry.position === 'string' && entry.position.includes('Found') ? 'HIT' : entry.position}
                                        </span>
                                        {entry.position !== '1000+' && <ArrowUpRight className="w-3 h-3 text-emerald-500" />}
                                    </div>
                                </motion.div>
                            ))}
                            {history.length === 0 && (
                                <div className="text-center py-20 bg-white/[0.01] border-2 border-dashed border-white/5 rounded-2xl">
                                    <p className="text-[9px] font-black uppercase text-slate-700 tracking-widest leading-none">Log Empty</p>
                                </div>
                            )}
                        </div>
                    </section>

                    <div className="bg-blue-600/5 border border-blue-600/10 p-6 rounded-2xl space-y-4">
                        <div className="flex items-center gap-2">
                            <ShieldCheck className="w-4 h-4 text-blue-400" />
                            <span className="text-[10px] font-black text-white uppercase tracking-widest">Active Scan Protocol</span>
                        </div>
                        <p className="text-[10px] text-slate-500 font-bold leading-relaxed">
                            Deep scanning is now active across 1000+ nodes. Quantum Permutations will automatically trigger if primary indexing is invisible.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default function KeywordTrackerPage() {
    return (
        <Suspense fallback={<div className="flex justify-center py-20"><Loader2 className="w-10 h-10 animate-spin text-primary" /></div>}>
            <TrackerContent />
        </Suspense>
    )
}
